import * as React from "react";
import { Helmet } from "react-helmet";
import Navbar from '../components/navbar';
import Nri from '../components/nri';
import Contact from '../components/contact';
import Footer from '../components/footer';
import "/src/css/custom.css";
import { useCookies } from 'react-cookie';

// styles
const pageStyles = {
    color: "#232129",
    fontFamily: "-apple-system, Roboto, sans-serif, serif",
}

// markup
const NriPage = () => {
    
    const [cookies, setCookie] = useCookies(['logo']);

    // ----- Logo -----//
    
    let project = "";
    let project_id = "";
    let contact = "";
    let logo = cookies.logo;
    let rerano = "";
    let disclaimer = "";
    let copyright = "";

    if (typeof window !== 'undefined') {
        project = localStorage.getItem('project');
        project_id = localStorage.getItem('project_id');
        contact = localStorage.getItem('contact');
        //logo = localStorage.getItem('logo');
        rerano = localStorage.getItem('rerano');
        disclaimer = localStorage.getItem('disclaimer');
        copyright = localStorage.getItem('copyright');
        contact = JSON.parse(contact);
    }
    

    return (
        <main style={pageStyles}>
            <Helmet>
                <title>{project}</title>
            </Helmet>

            <Navbar logo="https://ginfomedia.in/production/godrejkolshet/wp-content/uploads/2022/04/Godrej-kolshet-logo-1-300x77-1.png" />
            <Nri />
            <Contact contact={contact} project_id={project_id} project={project} />
            <Footer rerano={rerano} disclaimer={disclaimer} copyright={copyright} />

        </main>

    )

}

export default NriPage
